<!--全部、来信、来访、网信切换-->
<template>
  <ul class="typeChange">
    <li
      v-for="(item, index) in arr"
      :key="index"
      @click="changeType(item)"
      :class="curType === item.name ? 'active' : ''"
    >{{item.name}}
    </li>
  </ul>
</template>

<script>
  export default {
    name: "index",
    data() {
      return {
        arr: [
          {
            "name": "信访量",
            "id": "信访量"
          },
          {
            "name": "满意率-信访部门",
            "id": "满意率-信访部门"
          },
          {
            "name": "满意率-责任单位",
            "id": "满意率-责任单位"
          },
          {
            "name": "化解率",
            "id": "化解率"
          }
        ],
        curType: "信访量",
        curTypeId: "信访量",
      }
    },
    // mounted(){
    //   this.$emit("change", this.curTypeId);
    // },
    methods: {
      changeType(item) {
        this.curType = item.name;
        this.curTypeId = item.id;
        this.$emit("change", this.curTypeId);
      }
    }
  }
</script>

<style lang="stylus" scoped>
  .typeChange
    width 100%
    display flex
    flex-direction row
    align-items center
    li
      flex 1
      padding .1rem .2rem
      border 1px #4295dc solid
      margin .15rem .2rem 0
      text-align center
      border-radius .2rem
      color #fff
      font-size 0.16rem
      cursor pointer
      background rgba(000,000,000,.6)
      &.active
        color #8df6ff
        font-weight 700
        text-shadow 0 0 5px #0cafc9
</style>
